@mixin flex-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
// * {
//   margin: 0;
//   padding: 0;
//   box-sizing: border-box !important;
// }
main {
  // background-color: #f0a2a2;
  min-height: 65vh;
  min-width: 100%;
  // padding: 25px 0px;
  @include flex-center();
  // padding: 30px 0px;

  .input {
    // background-color: rgb(236, 12, 12);
    @include flex-center();
    height: 250px;

    .input-field {
      // background-color: #236891;
      width: 90%;
    }
  }
  .output {
    // background-color: #786fae;
    height: 250px;
    @include flex-center();
    flex-wrap: wrap;

    // display: none;

    // padding-top: 50px;
    .result {
      // display: none;
      width: 90%;
      // min-height: 250px;
      padding: 0px 0px;
      background-color: #d69d66;
      // @include flex-center();
      box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.2);

      border-radius: 10px;
    }
  }
}

